.cartItems {
	flex: 1;
	overflow: auto;
	margin-bottom: 40px;
}

.cartTotal {
	ul {
		margin-bottom: 40px;

		li {
			display: flex;
			align-items: flex-end;
			margin-bottom: 20px;

			div {
				flex: 1;
				height: 1px;
				border-bottom: 1px dashed #dfdfdf;
				position: relative;
				top: -4px;
				margin: 0 7px;
			}
		}
	}
}
